
import { defineComponent, ref, watch, customRef } from 'vue'

function useDebouncedRef (value: string, delay = 200) {
  let timeout: number
  return customRef((track, trigger) => {
    return {
      get () {
        track()
        return value
      },
      set (newValue: string) {
        clearTimeout(timeout)
        timeout = setTimeout(() => {
          value = newValue
          trigger()
        }, delay)
      },
    }
  })
}

export default defineComponent({
  name: 'search-box',
  props: {
    placeholder: {
      type: String,
      default: '搜索歌曲、歌手'
    }
  },
  setup (props, {emit}) {
    const query = ref('')
    const queryRef = ref('' as unknown as HTMLInputElement)
    const clear = () => {
      query.value = ''
    }
    const blur = () => {
      queryRef.value.blur()
    }
    const setQuery = (q: string) => {
      query.value = q
    }
    watch((() => query.value), (newQuery) => {
      emit('query', newQuery)
    })
    return {
      query,
      clear,
      blur,
      setQuery
    }
  }
})
