
import { defineComponent, ref } from 'vue'

export default defineComponent({
  props: {
    text: {
      type: String,
      default: ''
    },
    confirmBtnText: {
      type: String,
      default: '确定'
    },
    cancelBtnText: {
      type: String,
      default: '取消'
    }
  },
  setup (props, {emit}) {
    const showFlag = ref(false)
    const show = () => {
      showFlag.value = true
    }
    const hide = () => {
      showFlag.value = false
    }
    const cancel = () => {
      hide()
      emit('cancel')
    }
    const confirm = () => {
      hide()
      emit('confirm')
    }
    return {
      showFlag,
      show,
      hide,
      cancel,
      confirm
    }
  }
})
