
import { defineComponent } from 'vue'
import { useStorageInject } from "@/store/stroageStore"

export default defineComponent({
  name: 'search-list',
  props: {
    searches: {
      type: Array,
      default () {
        return []
      }
    }
  },
  setup (props, {emit}) {
    const {deleteSearch} = useStorageInject()
    const selectItem = (item: string) => {
      emit('select', item)
    }
    const deleteOne = (item: string) => {
      deleteSearch(item)
    }
    return {
      selectItem,
      deleteOne
    }
  }
})
